"use client";
import { useState } from "react";
import { TrendingUp, Calendar, Clock, DollarSign, Package, Loader2, CheckCircle, XCircle, Database, Table } from "lucide-react";

type NotificationType = {
  type: "success" | "error";
  message: string;
};

export default function Home() {
  const [buyerPrice, setBuyerPrice] = useState("");
  const [buyerQuantity, setBuyerQuantity] = useState("");
  const [buyerDate, setBuyerDate] = useState("");
  const [buyerTime, setBuyerTime] = useState("");
  const [buyerLoading, setBuyerLoading] = useState(false);

  const [sellerPrice, setSellerPrice] = useState("");
  const [sellerQuantity, setSellerQuantity] = useState("");
  const [sellerDate, setSellerDate] = useState("");
  const [sellerTime, setSellerTime] = useState("");
  const [sellerLoading, setSellerLoading] = useState(false);

  const [notification, setNotification] = useState<NotificationType | null>(null);

  const showNotification = (type: "success" | "error", message: string) => {
    setNotification({ type, message });
    setTimeout(() => setNotification(null), 4000);
  };

  const handleBuySubmit = async () => {
    setBuyerLoading(true);

    try {
      const res = await fetch("/api/trades", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          role: "buyer",
          price: Number(buyerPrice),
          quantity: Number(buyerQuantity),
          trade_date: buyerDate,
          trade_time: buyerTime,
        }),
      });

      const data = await res.json();
      if (data.success) {
        showNotification("success", `Buy order added successfully!`);
        setBuyerPrice("");
        setBuyerQuantity("");
        setBuyerDate("");
        setBuyerTime("");
        setBuyerLoading(false);
      } else {
        showNotification("error", data.error || "Failed to add buy order. Please try again.");
        setBuyerLoading(false);
      }
    } catch (error) {
      showNotification("error", "Network error. Please check your connection and try again.");
      setBuyerLoading(false);
    }
  };

  const handleSellSubmit = async () => {
    setSellerLoading(true);

    try {
      const res = await fetch("/api/trades", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          role: "seller",
          price: Number(sellerPrice),
          quantity: Number(sellerQuantity),
          trade_date: sellerDate,
          trade_time: sellerTime,
        }),
      });

      const data = await res.json();
      if (data.success) {
        showNotification("success", `Sell order added successfully!`);
        setSellerPrice("");
        setSellerQuantity("");
        setSellerDate("");
        setSellerTime("");
        setSellerLoading(false);
      } else {
        showNotification("error", data.error || "Failed to add sell order. Please try again.");
        setSellerLoading(false);
      }
    } catch (error) {
      showNotification("error", "Network error. Please check your connection and try again.");
      setSellerLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      {notification && (
        <div className="fixed top-4 right-4 z-50 transition-all duration-300 ease-out">
          <div className="flex items-center gap-3 px-6 py-4 rounded-lg shadow-2xl border-2 bg-white border-gray-300">
            {notification.type === "success" ? (
              <CheckCircle className="text-black flex-shrink-0" size={24} />
            ) : (
              <XCircle className="text-black flex-shrink-0" size={24} />
            )}
            <div className="max-w-xs">
              <p className="font-semibold text-black">
                {notification.type === "success" ? "Success!" : "Error"}
              </p>
              <p className="text-sm text-gray-600">
                {notification.message}
              </p>
            </div>
          </div>
        </div>
      )}

      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-8">
          <div className="flex items-center justify-center gap-3 mb-2">
            <TrendingUp className="text-black" size={40} />
            <h1 className="text-4xl font-bold text-black">Trade Platform</h1>
          </div>
          <p className="text-gray-600">Place your buy or sell orders</p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          <div className="bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black">
            <div className="px-6 py-5">
              <h2 className="text-2xl font-bold text-black">Buyers</h2>
            </div>

            <div className="px-6 py-6 space-y-5">
              <div>
                <label className="block text-sm font-semibold text-black mb-2">
                  Buying Price
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={buyerPrice}
                    onChange={(e) => setBuyerPrice(e.target.value)}
                    step="0.01"
                    placeholder="e.g. 123.45"
                    disabled={buyerLoading}
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none bg-white text-black placeholder-gray-400 disabled:opacity-50 disabled:cursor-not-allowed"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-semibold text-black mb-2">
                  Quantity
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={buyerQuantity}
                    onChange={(e) => setBuyerQuantity(e.target.value)}
                    placeholder="e.g. 10"
                    disabled={buyerLoading}
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none bg-white text-black placeholder-gray-400 disabled:opacity-50 disabled:cursor-not-allowed"
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-semibold text-black mb-2">
                    Date
                  </label>
                  <div className="relative">
                    <input
                      type="date"
                      value={buyerDate}
                      onChange={(e) => setBuyerDate(e.target.value)}
                      disabled={buyerLoading}
                      className="w-full px-3 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none text-sm bg-white text-black disabled:opacity-50 disabled:cursor-not-allowed"
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-semibold text-black mb-2">
                    Time
                  </label>
                  <div className="relative">
                    <input
                      type="time"
                      value={buyerTime}
                      onChange={(e) => setBuyerTime(e.target.value)}
                      step="1"
                      disabled={buyerLoading}
                      className="w-full px-3 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none text-sm bg-white text-black disabled:opacity-50 disabled:cursor-not-allowed"
                    />
                  </div>
                </div>
              </div>

              <button
                onClick={handleBuySubmit}
                disabled={buyerLoading || !buyerPrice || !buyerQuantity || !buyerDate || !buyerTime}
                className={`w-full font-bold py-4 px-6 rounded-lg border-2 transform hover:scale-105 transition-all duration-200 shadow-lg hover:shadow-xl disabled:cursor-not-allowed disabled:transform-none flex items-center justify-center gap-2 ${
                  buyerPrice && buyerQuantity && buyerDate && buyerTime && !buyerLoading
                    ? "bg-black text-white border-black hover:bg-gray-800"
                    : "bg-white text-black border-black opacity-50"
                }`}
              >
                {buyerLoading ? (
                  <>
                    <Loader2 className="animate-spin" size={20} />
                    Placing Buy...
                  </>
                ) : (
                  "Place Buy"
                )}
              </button>
            </div>
          </div>

          <div className="bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black">
            <div className="px-6 py-5">
              <h2 className="text-2xl font-bold text-black">Sellers</h2>
            </div>

            <div className="px-6 py-6 space-y-5">
              <div>
                <label className="block text-sm font-semibold text-black mb-2">
                  Selling Price
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={sellerPrice}
                    onChange={(e) => setSellerPrice(e.target.value)}
                    step="0.01"
                    placeholder="e.g. 125.00"
                    disabled={sellerLoading}
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none bg-white text-black placeholder-gray-400 disabled:opacity-50 disabled:cursor-not-allowed"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-semibold text-black mb-2">
                  Quantity
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={sellerQuantity}
                    onChange={(e) => setSellerQuantity(e.target.value)}
                    placeholder="e.g. 5"
                    disabled={sellerLoading}
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none bg-white text-black placeholder-gray-400 disabled:opacity-50 disabled:cursor-not-allowed"
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-semibold text-black mb-2">
                    Date
                  </label>
                  <div className="relative">
                    <input
                      type="date"
                      value={sellerDate}
                      onChange={(e) => setSellerDate(e.target.value)}
                      disabled={sellerLoading}
                      className="w-full px-3 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none text-sm bg-white text-black disabled:opacity-50 disabled:cursor-not-allowed"
                    />
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-semibold text-black mb-2">
                    Time
                  </label>
                  <div className="relative">
                    <input
                      type="time"
                      value={sellerTime}
                      onChange={(e) => setSellerTime(e.target.value)}
                      step="1"
                      disabled={sellerLoading}
                      className="w-full px-3 py-3 border-2 border-gray-300 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all outline-none text-sm bg-white text-black disabled:opacity-50 disabled:cursor-not-allowed"
                    />
                  </div>
                </div>
              </div>

              <button
                onClick={handleSellSubmit}
                disabled={sellerLoading || !sellerPrice || !sellerQuantity || !sellerDate || !sellerTime}
                className={`w-full font-bold py-4 px-6 rounded-lg border-2 transform hover:scale-105 transition-all duration-200 shadow-lg hover:shadow-xl disabled:cursor-not-allowed disabled:transform-none flex items-center justify-center gap-2 ${
                  sellerPrice && sellerQuantity && sellerDate && sellerTime && !sellerLoading
                    ? "bg-black text-white border-black hover:bg-gray-800"
                    : "bg-white text-black border-black opacity-50"
                }`}
              >
                {sellerLoading ? (
                  <>
                    <Loader2 className="animate-spin" size={20} />
                    Placing Sell...
                  </>
                ) : (
                  "Place Sell"
                )}
              </button>
            </div>
          </div>
        </div>

        {/* New Section: Database Tables Links */}
        <div className="mt-12 text-center">
          <h2 className="text-2xl font-bold text-black mb-6 flex items-center justify-center gap-3">
            <Database className="text-black" size={32} />
            View Database Tables (Real-Time)
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 max-w-4xl mx-auto">
            <a
              href="/trades"
              target="_blank"
              rel="noopener noreferrer"
              className="group bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black p-6 hover:shadow-xl transition-all duration-200 flex flex-col items-center text-center cursor-pointer"
            >
              <Table className="text-blue-600 group-hover:text-blue-700 mb-3" size={48} />
              <h3 className="text-xl font-bold text-black mb-2">Trades</h3>
              <p className="text-gray-600">View all trades with real-time updates</p>
            </a>
            <a
              href="/mainb"
              target="_blank"
              rel="noopener noreferrer"
              className="group bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black p-6 hover:shadow-xl transition-all duration-200 flex flex-col items-center text-center cursor-pointer"
            >
              <Table className="text-green-600 group-hover:text-green-700 mb-3" size={48} />
              <h3 className="text-xl font-bold text-black mb-2">MainB</h3>
              <p className="text-gray-600">View active MainB orders with real-time updates</p>
            </a>
            <a
              href="/matched-trades"
              target="_blank"
              rel="noopener noreferrer"
              className="group bg-white rounded-3xl shadow-lg overflow-hidden border-2 border-black p-6 hover:shadow-xl transition-all duration-200 flex flex-col items-center text-center cursor-pointer"
            >
              <Table className="text-purple-600 group-hover:text-purple-700 mb-3" size={48} />
              <h3 className="text-xl font-bold text-black mb-2">Matched Trades</h3>
              <p className="text-gray-600">View matched trades with real-time updates</p>
            </a>
          </div>
        </div>

        <p className="text-center text-gray-600 text-sm mt-8">
          All fields are required to submit a trade
        </p>
      </div>
    </div>
  );
}